.header {
	grid-row: 1;
	grid-column: 1/3;
	height: 60px;
	border-bottom: 1px solid rgb(231, 223, 223);
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 20px;
}
.header button {
	height: 40px;
}
.header span {
	font-size: 20px;
	font-weight: bold;
}
